.block-container {
    margin: 65px auto 0;
}

.block {
    padding: 30px;
    background-color: #f0f0df;
    border-radius: 6px;
}

.container-css {
    min-height:1000px;
}

.container-slideshow {
    height: 80vh;
    background: transparent;
    z-index:-99999999999;
}

.content-white {
    background-color: #3f5866;
}

.description {
    max-width: 500px;
    margin-left: auto;
    margin-right: auto;
    font-size: 30px;
    font-family: "Play";
}

.fade-in {
    background-color: #3f5866 !important;
    height: 100vh;
    font-size: 21px;
    margin-left: auto;
    margin-right: auto;
    -webkit-animation: fadein .5s; 
            animation: fadein .5s;
}

@keyframes fadein {
    from { opacity: 0; }
    to   { opacity: 1; }
}

@-webkit-keyframes fadein {
    from { opacity: 0; }
    to   { opacity: 1; }
}

.line-after-description-under-blue {
