  right: 0;
}

.marg-icon{
  margin-top: 95px;
}

.buttons-line-container {
    margin: 100px auto 0;
    width: 100vw;
    transition: .5s;
    background-color: #3f5866;
}

.buttons-line-container-smaller {
    margin: 0;
    width: 100vw;
    transition: .5s;
    background-image: 
        linear-gradient(
            to right, 
            #f0f0df 0%, 
            #000000 100%
            );    
    height: 60px;
}


.Home-content-name {
    transition: 0.5s;
    position: fixed;
    height: 100px;
    margin-top: -10px;
    background-color: #3f5866;
}

.Home-content-name-smaller {
    margin-left: auto;
    margin-right: auto;
    transition: 0.5s;
    height: 35px;
    width: 100vw;
    position: fixed;
}


.Home-header{
    background-size: cover;
    background-color: rgb(36, 11, 11);
    margin-left: auto;
    margin-right: auto;
    transition: 0.5s;
    position: fixed;
}

.Home-header-remove{
    transition: 0.5s;
    background-color: rgb(255, 255, 255);
    position: fixed;
}

.home-header-text{
    font-size: 80px;
    grid-area: home-header-text;
    width: 100vw;
    margin: -90px auto 0;
    font-family: "Playfair Display";
}

.Home-nav-buttons-after-small span {
    align-items: center;
    background: #3f5866;
    display: inline-block;
    height: 100%;
    transition: background .5s ease;
    width: 100%;
}

.Home-nav-buttons{
    background-image: 
        linear-gradient(
            to top, 
            #bdd3de 0%, 
            #3f5866 100%
            );    
    color: rgb(22, 21, 21);
    cursor: pointer;
    height: 45px;
    width: 65px;
    margin: -30px 0 0 600px;
    border: none;
    font-style: italic;
    font-size: 25px;
    grid-area: buttons;
    padding: 0 !important;
    z-index: 0;
    position: relative; 
}

.Home-nav-buttons:hover {
    color: #ff8f00;
}


.Home-nav-buttons span {
	align-items: center;
	background: #3f5866;;
	display: inline-block;
	height: 100%;
	transition: background .5s ease;
	width: 100%;
}

.Home-nav-buttons:hover span {
	background: transparent;
} 

.Home-nav-buttons-after{
    background-image: 
        linear-gradient(
            to top, 
            #bdd3de 0%, 
            #3f5866 100%
            );    
    color: rgb(22, 21, 21);
    cursor: pointer;
    height: 45px;
    width: 100px;
    margin-top: -30px;
    border: none;
    font-style: italic;
    font-size: 25px;
    padding: 0 !important;
    z-index: 0;
    position: relative; 
}

.Home-nav-buttons-after:hover {
    color: #ff8f00;
}


.Home-nav-buttons-after span {
	align-items: center;
	background: #3f5866;;
	display: inline-block;
	height: 100%;
	transition: background .5s ease;
	width: 100%;
}

.Home-nav-buttons-after:hover span {
	background: transparent;
} 

.background-la {
    background-image: url(/allanyeznaian/static/media/LA.43e45e6d.jpg);
    background-size: cover;
    position: fixed;
    background-position: center;
    background-repeat: no-repeat;
    height: 100%;
    width: 100%;
}

.chase-animation {
    position: absolute;
    -webkit-animation: chase-container 1s;
    animation: chase-container 1s;
    background-color: rgba(255, 255, 255, 0.63);
    padding: 5px;
    border: solid 3px rgb(47, 0, 255);
    border-left: none !important;
}

@-webkit-keyframes chase-container
{
    from {
        left: -100px;
    }
    to {
        left: 0;
    }
}
@keyframes chase-container
{
    from {
        left: -100%;
    }
    to {
        left: 0;
    }
}

.chase-container {
    background-color: white;
    width: 30%;
    position: fixed;
    margin-top: 50px;
    margin-left: 0px;
    
}

.chase-fadout {
    
    background-color: rgba(255, 255, 255, 0.438) !important;
    min-height: 2900px;
    min-width: 100vw;
    font-size: 21px;
    -webkit-animation: fadeout 2s; 
            animation: fadeout 2s;
}    
@keyframes fadeout {
    from { opacity: 0; }
    to   { opacity: 1; }
}

@-webkit-keyframes fadeout {
    from { opacity: 0; }
    to   { opacity: 1; }
}

.fa-close {
    color: red;
    cursor: pointer;
    float: right;
}

.fa-close:hover {
    color: rgba(255, 0, 0, 0.548);
    cursor: pointer;
    float: right;
}
@media (max-width: 1000px) 
{
    
    .music {
        font-size: 14px;
    }
  }

@media (max-width: 600px) {

    .automobility-img {
        width: 35%;
        margin: auto;
    }

    .block {
        width: 80%;
        height: 100%;
    }

    .description {
        max-width: 80%;
        font-size: 25px;
    }
    .home-header-text{
        font-size: 1.5em;
        margin: -50px auto 40px 10px;
    }

    .icon-wrapper {
        width: 30px;
    }

    .music-description-2 {
        margin: auto auto 30px auto;
    }

    .Recruit-Hub-image {
        width: 35%;
        margin: auto;
    }
    
    .wrapper-info {
        display: grid;
        grid-template-columns:  100%;
        margin: 1em 20px 0 20px;
    }
}
