@media (max-width: 600px) {

    .automobility-img {
        width: 35%;
        margin: auto;
    }

    .block {
        width: 80%;
        height: 100%;
    }

    .description {
        max-width: 80%;
        font-size: 25px;
    }
    .home-header-text{
        font-size: 1.5em;
        margin: -50px auto 40px 10px;
    }

    .icon-wrapper {
        width: 30px;
    }

    .music-description-2 {
        margin: auto auto 30px auto;
    }

    .Recruit-Hub-image {
        width: 35%;
        margin: auto;
    }
    
    .wrapper-info {
        display: grid;
        grid-template-columns:  100%;
        margin: 1em 20px 0 20px;
    }
}